document.addEventListener('click', (e) => {
  const click = e.target;
  if (click.classList.contains('search-input')) {
    return;
  }
  if (!click.classList.contains('bi')) {
    // document.querySelector('.button-menu').classList.remove('active');
    // document.querySelector('.button-menu').classList.remove('bg-slate-700');
    // document.querySelector('.container-menu').classList.remove('w-[300px]');
    // document.querySelector('.container-menu').classList.add('w-0');
    // document.querySelector('.icon-menu').classList.add('bi-list');
    // document.querySelector('.icon-menu').classList.remove('bi-x');
    document.querySelector('.button-search').classList.remove('active');
    document.querySelector('.button-search').classList.remove('bg-slate-700');
    document.querySelector('.container-search').classList.remove('w-full');
    document.querySelector('.container-search').classList.add('w-0');
    document.querySelector('.icon-search').classList.remove('bi-x-lg');
    document.querySelector('.icon-search').classList.add('bi-search');
  }
});

